<template>
  <a-modal
    :title="title"
    :visible="visible"
    :keyboard="false"
    :maskClosable="false"
    okText=""
    okType="primary"
    @ok="handleProcessCheckNoticeOk()"
    @cancel="handleProcessCheckNoticeCancel()">
    <a-form
      :form="form">
      <a-form-item
        label=""
        v-bind="layout">
        <a-input
          type="text"
          v-decorator="[
            'id',
            {
              rules: [
                { max:256, message: '256' },
                {required: true, message: ''}
              ],
              validateTrigger: 'blur',
              initialValue: record ? record.id : null
            }
          ]">
        </a-input>
      </a-form-item>
      <a-form-item
        label=""
        v-bind="layout">
        <a-input
          type="text"
          v-decorator="[
            'part',
            {
              rules: [
                { max:256, message: '256' },
                {required: true, message: ''}
              ],
              validateTrigger: 'blur',
              initialValue: record ? record.part : null
            }
          ]">
        </a-input>
      </a-form-item>
      <a-form-item
        label=""
        v-bind="layout">
        <a-date-picker
          style="width:100%"
          v-decorator="[
                'give_time',
                {rules: [{type: 'object', required: true, message: ''}],
                initialValue: record ? record.give_time : null}
              ]"></a-date-picker>
      </a-form-item>
      <a-form-item
        label=""
        v-bind="layout">
        <a-date-picker
          style="width:100%"
          v-decorator="[
                'reply_time',
                {rules: [],
                initialValue: record ? record.reply_time : null}
              ]"></a-date-picker>
      </a-form-item>
    </a-form>
  </a-modal>
</template>

<script>
import api from '../../../../../api/supervisorSecurity'
import { mapGetters } from 'vuex'
import moment from 'moment'

export default {
  name: 'ProcessNoticeModal',
  props: {
    record: Object,
    visible: Boolean,
    type: String,
  },
  data () {
    return {
      project_id: this.projectSelected().id,
      layout: {
        'label-col': { span: 8 },
        'wrapper-col': { span: 12 }
      },
      form: this.$form.createForm(this),
      matter: {}
    }
  },
  mounted(){
    this.getData()
  },
  methods: {
    ...mapGetters(['projectSelected']),
    getData(){
      if(this.isEdit){
        this.record.give_time=new moment(this.record.give_time)
        if(this.record.reply_time!==null) {
          this.record.reply_time = new moment(this.record.reply_time)
        }
      }
    },
    handleProcessCheckNoticeOk () {
      this.form.validateFields((error, data) => {
        if (!error) {
          data.give_time = data.give_time.format('YYYY-MM-DD')
          if(data.reply_time!=null) {
            data.reply_time = data.reply_time.format('YYYY-MM-DD')
          }
          if (this.isEdit) {
            api.updateProcessCheckNotice(this.record.id, data)
              .then(data => {
                this.$notification.success({ message: '', description: '', key: 'SUCCESS' })
                this.$emit('close')
              })
          } else {
            api.createProcessCheckNotice(this.project_id, data)
              .then(data => {
                this.$notification.success({ message: '', description: '', key: 'SUCCESS' })
                this.$emit('close')
              })
          }
        }
      })
    },
    handleProcessCheckNoticeCancel () {
      this.$emit('close')
    },
    validateProgressLimit (rule, value, callback) {
      if (this.isEdit) {
        callback()
      } else {
        if (value < 0 || value > 100)
          callback('false')
        else
          callback()
      }
    }
  },
  computed: {
    isEdit () {
      return this.type === 'edit'
    },
    title () {
      return this.isEdit ? '' : ''
    }
  }
}
</script>

